import React, { useEffect, useState } from 'react';
import { useAuth } from '../src/context/AuthContext';
import { useNavigate } from 'react-router-dom';
import { useI18n } from '../src/lib/useI18n';
import AssignmentModal from '../components/AssignmentModal';
import { fetchWithRefresh } from '../utils/fetchWithRefresh';

const API_URL = import.meta.env.VITE_API_URL;

interface Nanny {
  id: string;
  name: string;
  availability: string;
}

interface Assignment {
  id: string;
  date: string; 
  child: { id: string; name: string };
  nanny: { id: string; name: string };
}

interface AssignmentForm {
  date: string;
  childId: string;
  nannyId: string;
}



function getMonthGrid(date: Date) {
  const year = date.getFullYear();
  const month = date.getMonth();
  const first = new Date(year, month, 1);
  const last = new Date(year, month + 1, 0);
  const start = new Date(first);
  start.setDate(first.getDate() - ((first.getDay() + 6) % 7));
  const end = new Date(last);
  end.setDate(last.getDate() + (7 - end.getDay() === 7 ? 0 : 7 - end.getDay()));
  const days: Date[] = [];
  for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
    days.push(new Date(d));
  }
  const weeks: Date[][] = [];
  for (let i = 0; i < days.length; i += 7) {
    weeks.push(days.slice(i, i + 7));
  }
  return weeks;
}

export default function Dashboard() {
  const { user } = useAuth();
  const navigate = useNavigate();
  const { t, locale } = useI18n();
  const [isShortLandscape, setIsShortLandscape] = useState(false);
  useEffect(() => {
    if (user && user.nannyId) {
      navigate('/mon-planning', { replace: true });
    }
  }, [user, navigate]);

  useEffect(() => {
    if (typeof window === 'undefined' || typeof window.matchMedia !== 'function') return;
    const mql = window.matchMedia('(max-height: 600px) and (orientation: landscape)');
    const onChange = () => setIsShortLandscape(Boolean(mql.matches));
    onChange();
    if (typeof mql.addEventListener === 'function') mql.addEventListener('change', onChange); else mql.addListener(onChange);
    window.addEventListener('resize', onChange);
    window.addEventListener('orientationchange', onChange);
    return () => { try { if (typeof mql.removeEventListener === 'function') mql.removeEventListener('change', onChange); else mql.removeListener(onChange); } catch { /* ignore */ } window.removeEventListener('resize', onChange); window.removeEventListener('orientationchange', onChange); };
  }, []);
  const [assignments, setAssignments] = useState<Assignment[]>([]);
  const [childrenCount, setChildrenCount] = useState<number>(0);
  const [activeCaregivers, setActiveCaregivers] = useState<number>(0);
  const [childrenChangePercent, setChildrenChangePercent] = useState<number | null>(null);
  const [weeklyChangePercent, setWeeklyChangePercent] = useState<number | null>(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalInitial, setModalInitial] = useState<AssignmentForm | null>(null);
  const [selectedId, setSelectedId] = useState<string | null>(null);
  const [currentDate, setCurrentDate] = useState(new Date());
  const [dayModalOpen, setDayModalOpen] = useState(false);
  const [dayModalAssignments, setDayModalAssignments] = useState<Assignment[]>([]);
  const [dayModalDate, setDayModalDate] = useState<string>('');

  const fetchAssignments = React.useCallback((start?: Date, end?: Date) => {
    let url = `${API_URL}/assignments`;
    const params: string[] = [];
    if (start && end) {
      params.push(`start=${start.toISOString()}`);
      params.push(`end=${end.toISOString()}`);
    }
    if (user && user.role === 'nanny' && user.nannyId) {
      params.push(`nannyId=${user.nannyId}`);
    }
    if (params.length > 0) {
      url += '?' + params.join('&');
    }
    fetchWithRefresh(url, { credentials: 'include' })
      .then(res => res.json())
      .then(data => {
        setAssignments(data);
      });
  }, [user]);

  useEffect(() => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    const first = new Date(year, month, 1);
    const last = new Date(year, month + 1, 0);

    fetchWithRefresh(`${API_URL}/assignments?start=${first.toISOString()}&end=${last.toISOString()}`, { credentials: 'include' })
      .then(res => res.json())
      .then((data: Assignment[]) => {
        setAssignments(data);
        const uniqueThis = new Set((data || []).map(a => a.child.id)).size;

        const prevFirst = new Date(year, month - 1, 1);
        const prevLast = new Date(year, month, 0);
        fetchWithRefresh(`${API_URL}/assignments?start=${prevFirst.toISOString()}&end=${prevLast.toISOString()}`, { credentials: 'include' })
          .then(r => r.json())
          .then((prevData: Assignment[]) => {
            const uniquePrev = new Set((prevData || []).map(a => a.child.id)).size;
            if (uniquePrev === 0) {
              setChildrenChangePercent(null);
            } else {
              const diff = Math.round(((uniqueThis - uniquePrev) / uniquePrev) * 100);
              setChildrenChangePercent(diff);
            }
          })
          .catch(() => setChildrenChangePercent(null));

        const today = new Date();
        const last7Days: string[] = [];
        for (let i = 6; i >= 0; i--) {
          const d = new Date(today);
          d.setDate(today.getDate() - i);
          const dayOfWeek = d.getDay();
          if (dayOfWeek >= 1 && dayOfWeek <= 5) {
            last7Days.push(d.toISOString().split('T')[0]);
          }
        }
        const totalChildrenLocal = Array.isArray(data) ? Array.from(new Set(data.map(a => a.child.id))).length : 0;
        const dailyRates = last7Days.map(dateStr => {
          const present = new Set((data || []).filter(a => a.date.split('T')[0] === dateStr).map(a => a.child.id)).size;
          return totalChildrenLocal > 0 ? (present / totalChildrenLocal) * 100 : 0;
        });
        const currentWeeklyAvg = dailyRates.length > 0 ? Math.round(dailyRates.reduce((a, b) => a + b, 0) / dailyRates.length) : 0;

        const prevWeekDays = last7Days.map(d => {
          const nd = new Date(d);
          nd.setDate(nd.getDate() - 7);
          return nd.toISOString().split('T')[0];
        });
        const prevStart = new Date(prevWeekDays[0]);
        const prevEnd = new Date(prevWeekDays[prevWeekDays.length - 1]);
        fetchWithRefresh(`${API_URL}/assignments?start=${new Date(prevStart).toISOString()}&end=${new Date(prevEnd).toISOString()}`, { credentials: 'include' })
          .then(r => r.json())
          .then((prevWeekData: Assignment[]) => {
            const prevDailyRates = prevWeekDays.map(dateStr => {
              const present = new Set((prevWeekData || []).filter(a => a.date.split('T')[0] === dateStr).map(a => a.child.id)).size;
              return totalChildrenLocal > 0 ? (present / totalChildrenLocal) * 100 : 0;
            });
            const prevWeeklyAvg = prevDailyRates.length > 0 ? Math.round(prevDailyRates.reduce((a, b) => a + b, 0) / prevDailyRates.length) : 0;
            if (prevWeeklyAvg === 0) {
              setWeeklyChangePercent(null);
            } else {
              const wdiff = Math.round(((currentWeeklyAvg - prevWeeklyAvg) / prevWeeklyAvg) * 100);
              setWeeklyChangePercent(wdiff);
            }
          })
          .catch(() => setWeeklyChangePercent(null));

      })
      .catch(() => setAssignments([]));

    fetchWithRefresh(`${API_URL}/children`, { credentials: 'include' })
      .then(res => res.json())
      .then(data => setChildrenCount(Array.isArray(data) ? data.length : 0))
      .catch(() => setChildrenCount(0));

    fetch(`${API_URL}/nannies`, { credentials: 'include' })
      .then(res => res.json())
      .then((data: Nanny[]) => {
        if (Array.isArray(data)) {
          setActiveCaregivers(data.filter((n: Nanny) => n.availability === 'Disponible').length);
        } else {
          setActiveCaregivers(0);
        }
      })
      .catch(() => setActiveCaregivers(0));
  }, [currentDate, fetchAssignments]);

  const totalChildren = childrenCount;
  const today = new Date();
  const todayStr = today.toISOString().split('T')[0];
  const presentToday = Array.from(
    new Set(
      assignments
        .filter(a => a.date.split('T')[0] === todayStr)
        .map(a => a.child.id)
    )
  ).length;
  let weeklyAverage = 0;
  if (totalChildren > 0) {
    const last7Days: string[] = [];
    for (let i = 6; i >= 0; i--) {
      const d = new Date(today);
      d.setDate(today.getDate() - i);
      const dayOfWeek = d.getDay();
      if (dayOfWeek >= 1 && dayOfWeek <= 5) {
        last7Days.push(d.toISOString().split('T')[0]);
      }
    }
    const dailyRates = last7Days.map(dateStr => {
      const present = new Set(assignments.filter(a => a.date.split('T')[0] === dateStr).map(a => a.child.id)).size;
      return (present / totalChildren) + 100;
    });
    weeklyAverage = dailyRates.length > 0 ? Math.round(dailyRates.reduce((a, b) => a + b, 0) / dailyRates.length) : 0;
  }

  const monthGrid = getMonthGrid(currentDate);

  const presentRate = totalChildren > 0 ? Math.round((presentToday / totalChildren) * 100) : 0;

  const handlePrevMonth = () => {
    const prev = new Date(currentDate);
    prev.setMonth(prev.getMonth() - 1);
    setCurrentDate(prev);
  };
  const handleNextMonth = () => {
    const next = new Date(currentDate);
    next.setMonth(next.getMonth() + 1);
    setCurrentDate(next);
  };

  const handleQuickAdd = (date: Date) => {
    // Parents are not allowed to add assignments â€” prevent opening the modal
    if (user && user.role === 'parent') return;
    const localDate = new Date(date.getTime() - date.getTimezoneOffset() * 60000)
      .toISOString().split('T')[0];
    setModalInitial({ date: localDate, childId: '', nannyId: user && user.nannyId ? user.nannyId : '' });
    setModalOpen(true);
  };

  const [saveError, setSaveError] = useState<string | null>(null);
  const handleSave = async (data: AssignmentForm) => {
    setSaveError(null);
    const utcDate = new Date(data.date + 'T00:00:00Z').toISOString().split('T')[0];
    const dataToSend = { ...data, date: utcDate };
    const duplicate = assignments.some(a =>
      a.child.id === data.childId &&
      a.date.split('T')[0] === utcDate &&
      (!selectedId || a.id !== selectedId)
    );
    if (duplicate) {
      setSaveError("Cet enfant est dÃ©jÃ  affectÃ© ce jour-lÃ .");
      return;
    }
    if (selectedId) {
      await fetchWithRefresh(`${API_URL}/assignments/${selectedId}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify(dataToSend),
      });
      setModalOpen(false);
      setSelectedId(null);
      setModalInitial(null);
      const year = currentDate.getFullYear();
      const monthIdx = currentDate.getMonth();
      const first = new Date(year, monthIdx, 1);
      const last = new Date(year, monthIdx + 1, 0);
      fetchAssignments(first, last);
    } else {
      const res = await fetchWithRefresh(`${API_URL}/assignments`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify(dataToSend),
      });
      if (res.ok) {
        setModalOpen(false);
        setSelectedId(null);
        setModalInitial(null);
        const year = currentDate.getFullYear();
        const monthIdx = currentDate.getMonth();
        const first = new Date(year, monthIdx, 1);
        const last = new Date(year, monthIdx + 1, 0);
        fetchAssignments(first, last);
      } else {
        setSaveError("Erreur lors de l'ajout. Veuillez rÃ©essayer.");
      }
    }
  };

  const monthLabel = new Intl.DateTimeFormat(locale, { month: 'long', year: 'numeric' }).format(currentDate);
  // weekday short labels (Mon..Sun) based on currentDate week starting Monday
  const mondayRef = new Date(currentDate);
  mondayRef.setDate(currentDate.getDate() - ((currentDate.getDay() + 6) % 7));
  const shortWeekDays = Array.from({ length: 7 }).map((_, i) => {
    const d = new Date(mondayRef);
    d.setDate(mondayRef.getDate() + i);
    return new Intl.DateTimeFormat(locale, { weekday: 'short' }).format(d);
  });

  // currently selected assignment object (used to show child name in the delete modal)
  const selectedAssignment = selectedId ? assignments.find(a => a.id === selectedId) : undefined;

  return (
  <div className={`relative z-0 min-h-screen bg-[#fcfcff] p-4 ${!isShortLandscape ? 'md:pl-64' : ''} w-full`}>
      <div className="max-w-7xl mx-auto w-full">
      <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4 mb-4 w-full">
        <div className="flex-1 min-w-0">
          <h1 className="text-2xl md:text-3xl font-extrabold mb-1 tracking-tight" style={{ color: '#0b5566' }}>{t('page.dashboard')}</h1>
          <div className="text-base md:text-lg font-medium mb-4 md:mb-6" style={{ color: '#08323a' }}>{t('dashboard.welcome')}</div>
        </div>
          <div className="flex items-center gap-2 self-start md:self-end">
          <input type="date" value={currentDate.toISOString().split('T')[0]} onChange={e => setCurrentDate(new Date(e.target.value))}
            className="border border-gray-200 rounded-lg px-3 py-2 text-gray-700 bg-white shadow-sm text-base w-[120px] sm:w-auto" />
          {!(user && user.role === 'parent') && (
            <button onClick={() => handleQuickAdd(new Date())} className="bg-[#0b5566] text-white font-semibold rounded-lg px-4 py-2 text-base shadow hover:opacity-95 transition whitespace-nowrap">+ {t('global.add')}</button>
          )}
        </div>
      </div>
      <div className="grid grid-cols-1 gap-4 md:grid-cols-2 xl:grid-cols-4 mb-8 w-full">
        <div className="bg-white rounded-2xl shadow p-3 md:p-6 flex flex-col items-start gap-2 border border-[#f3f3fa] max-w-xs w-full">
            <div className="flex items-center gap-2 sm:gap-3">
            <span className="text-2xl sm:text-3xl font-bold text-gray-900">{totalChildren}</span>
            <span className="rounded-full p-2" style={{ background: '#a9ddf2', color: '#08323a' }}><svg width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><circle cx="12" cy="7" r="4"/><path d="M5.5 21a7.5 7.5 0 0 1 13 0"/></svg></span>
          </div>
          <div className="text-gray-500 font-medium text-sm sm:text-base">{t('dashboard.children_registered')}</div>
          <div className={`${childrenChangePercent !== null && childrenChangePercent < 0 ? 'text-red-500' : 'text-[#0b5566]'} text-xs sm:text-sm font-semibold flex items-center gap-1`}>{childrenChangePercent === null ? 'â€”' : `${childrenChangePercent > 0 ? '+' : ''}${childrenChangePercent}%`} {t('dashboard.since_last_month')}</div>
        </div>
        <div className="bg-white rounded-2xl shadow p-3 md:p-6 flex flex-col items-start gap-2 border border-[#f3f3fa] max-w-xs w-full">
          <div className="flex items-center gap-2 sm:gap-3">
            <span className="text-2xl sm:text-3xl font-bold text-gray-900">{presentToday}</span>
            <span className="bg-[#a9ddf2] text-[#0b5566] rounded-full p-2"><svg width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M5 13l4 4L19 7"/></svg></span>
          </div>
          <div className="text-gray-500 font-medium text-sm sm:text-base">{t('dashboard.present_today')}</div>
          <div className="text-[#0b5566] text-xs sm:text-sm font-semibold flex items-center gap-1">{t('dashboard.presence_rate')} {presentRate}%</div>
        </div>
        <div className="bg-white rounded-2xl shadow p-3 md:p-6 flex flex-col items-start gap-2 border border-[#f3f3fa] max-w-xs w-full">
            <div className="flex items-center gap-2 sm:gap-3">
            <span className="text-2xl sm:text-3xl font-bold text-gray-900">{activeCaregivers}</span>
            <span className="rounded-full p-2" style={{ background: '#f7f4d7', color: '#08323a' }}><svg width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M12 21C7 21 2 17 2 12V7a5 5 0 0 1 10 0v5c0 5-5 9-10 9z"/></svg></span>
          </div>
          <div className="text-gray-500 font-medium text-sm sm:text-base">{t('dashboard.active_caregivers')}</div>
          <div className="text-gray-400 text-xs sm:text-sm font-medium flex items-center gap-1">{t('dashboard.no_change')}</div>
        </div>
        <div className="bg-white rounded-2xl shadow p-3 md:p-6 flex flex-col items-start gap-2 border border-[#f3f3fa] max-w-xs w-full">
          <div className="flex items-center gap-2 sm:gap-3">
            <span className="text-2xl sm:text-3xl font-bold text-gray-900">{weeklyAverage}%</span>
            <span className="rounded-full p-2" style={{ background: '#fcdcdf', color: '#08323a' }}><svg width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><rect x="3" y="3" width="18" height="18" rx="4"/><path d="M8 17l4-4 4 4"/></svg></span>
          </div>
          <div className="text-gray-500 font-medium text-sm sm:text-base">{t('dashboard.weekly_average')}</div>
          <div className={`${weeklyChangePercent !== null && weeklyChangePercent < 0 ? 'text-red-500' : 'text-[#0b5566]'} text-xs sm:text-sm font-semibold flex items-center gap-1`}>{weeklyChangePercent === null ? 'â€”' : `${weeklyChangePercent > 0 ? '+' : ''}${weeklyChangePercent}%`} {t('dashboard.since_last_week')}</div>
        </div>
      </div>
      <div className="bg-white rounded-2xl shadow p-4 md:p-6 border border-[#f3f3fa] w-full mx-auto">
    <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-3 sm:mb-6 gap-2">
      <div className="text-lg md:text-2xl font-bold text-gray-900">{monthLabel.charAt(0).toUpperCase() + monthLabel.slice(1)}</div>
          <div className="flex items-center gap-2">
            <button onClick={handlePrevMonth} className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-200 bg-white hover:bg-gray-100 text-gray-500"><svg width="20" height="20" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M15 19l-7-7 7-7"/></svg></button>
            <button onClick={handleNextMonth} className="w-8 h-8 flex items-center justify-center rounded-full border border-gray-200 bg-white hover:bg-gray-100 text-gray-500"><svg width="20" height="20" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M9 5l7 7-7 7"/></svg></button>
          </div>
        </div>
        <div className="block sm:hidden w-full overflow-x-auto">
          <div className="grid grid-cols-7 gap-1 w-full">
            {shortWeekDays.map((day, i) => (
              <div key={i} className="text-center text-gray-500 font-semibold text-xs py-1">{day}</div>
            ))}
            {monthGrid.flat().map((day, idx) => {
              const assigns = assignments.filter(a => {
                const date = new Date(a.date);
                return (
                  date.getFullYear() === day.getFullYear() &&
                  date.getMonth() === day.getMonth() &&
                  date.getDate() === day.getDate()
                );
              });
              const isToday = day.toDateString() === new Date().toDateString();
              const isCurrentMonth = day.getMonth() === currentDate.getMonth();
              return (
                <div key={idx} className={
                  "align-top p-1 h-full flex flex-col " +
                  (isToday ? 'border-2 border-[#0b5566] rounded-xl ' : 'border border-gray-100 ') +
                  (isCurrentMonth ? 'bg-[#f8f8fc]' : 'bg-gray-50 opacity-60') +
                  " relative"
                }>
                  <div className="flex items-center justify-between mb-1">
                    <span
                      className={"text-xs font-bold cursor-pointer " + (isCurrentMonth ? 'text-gray-700' : 'text-gray-400')}
                      onClick={() => {
                        setDayModalAssignments(assigns);
                        setDayModalDate(new Intl.DateTimeFormat(locale).format(day));
                        setDayModalOpen(true);
                      }}
                      title="Voir la liste des enfants gardÃ©s ce jour"
                    >
                      {day.getDate()}
                    </span>
                          {!(user && user.role === 'parent') && (
                            <button onClick={() => handleQuickAdd(day)} className="text-[#0b5566] hover:text-[#08323a] text-lg font-bold">+</button>
                          )}
                  </div>
                  {assigns.length === 0 ? (
                    <div className="text-gray-300 text-xs">â€”</div>
                  ) : (
                        assigns.slice(0, 2).map((a, j) => (
                      <div key={a.id} className={"flex items-center gap-1 mb-1 px-1 py-1 rounded-lg " + (j === 0 ? 'bg-[#a9ddf2]' : 'bg-[#fff7e6]') + " shadow-sm group"}>
                        <span className={"w-2 h-2 rounded-full " + (j === 0 ? 'bg-[#08323a]' : 'bg-[#856400]')}></span>
                        <span
                          className="font-semibold text-gray-800 text-[11px] group-hover:underline cursor-pointer hover:text-red-600 truncate max-w-[70px]"
                          title={a.child.name}
                          onClick={() => { if (!(user && user.role === 'parent')) setSelectedId(a.id); }}
                        >
                          {a.child.name}
                        </span>
                      </div>
                    ))
                  )}
                  {assigns.length > 2 && (
                    <button
                      onClick={() => { setDayModalAssignments(assigns); setDayModalDate(new Intl.DateTimeFormat(locale).format(day)); setDayModalOpen(true); }}
                      className="text-xs text-gray-500 px-2 py-0.5 rounded-full bg-gray-100 hover:bg-gray-200"
                      title={t('common.view_more', { n: String(assigns.length - 2) })}
                    >
                      +{assigns.length - 2}
                    </button>
                  )}
                </div>
              );
            })}
          </div>
        </div>
        <div className="hidden sm:block w-full">
          <table className="w-full table-fixed bg-white rounded-lg">
            <thead>
              <tr>
                {shortWeekDays.map((day, i) => (
                  <th key={i} className="p-2 text-center text-gray-500 font-semibold text-base">
                    {day}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {monthGrid.map((week, wIdx) => (
                <tr key={wIdx}>
                  {week.map((day, dIdx) => {
                    const assigns = assignments.filter(a => {
                      const date = new Date(a.date);
                      return (
                        date.getFullYear() === day.getFullYear() &&
                        date.getMonth() === day.getMonth() &&
                        date.getDate() === day.getDate()
                      );
                    });
                    const isToday = day.toDateString() === new Date().toDateString();
                    const isCurrentMonth = day.getMonth() === currentDate.getMonth();
                    return (
                      <td key={dIdx} className={
                        "align-top p-2 h-28 " +
                        (isToday ? 'border-2 border-[#0b5566] rounded-xl ' : 'border border-gray-100 ') +
                        (isCurrentMonth ? 'bg-[#f8f8fc]' : 'bg-gray-50 opacity-60') +
                        " relative"
                      }>
                        <div className="flex items-center justify-between mb-1">
                          <span
                            className={"text-xs font-bold cursor-pointer " + (isCurrentMonth ? 'text-gray-700' : 'text-gray-400')}
                            onClick={() => {
                              setDayModalAssignments(assigns);
                              setDayModalDate(new Intl.DateTimeFormat(locale).format(day));
                              setDayModalOpen(true);
                            }}
                            title="Voir la liste des enfants gardÃ©s ce jour"
                          >
                            {day.getDate()}
                          </span>
                          {!(user && user.role === 'parent') && (
                            <button onClick={() => handleQuickAdd(day)} className="text-[#0b5566] hover:text-[#08323a] text-lg font-bold">+</button>
                          )}
                        </div>
                        {assigns.length === 0 ? (
                          <div className="text-gray-300 text-sm">â€”</div>
                        ) : (
                          <>
                            {assigns.slice(0, 2).map((a, j) => (
                            <div key={a.id} className={"flex items-center gap-2 mb-2 px-2 py-1 rounded-lg " + (j === 0 ? 'bg-[#a9ddf2]' : 'bg-[#f7f4d7]') + " shadow-sm group"}>
                                      <span className={"w-2 h-2 rounded-full " + (j === 0 ? 'bg-[#08323a]' : 'bg-[#856400]')}></span>
                                <span
                                      className="font-semibold text-gray-800 text-sm group-hover:underline cursor-pointer hover:text-red-600 truncate max-w-[120px]"
                                      title={a.child.name}
                                      onClick={() => { if (!(user && user.role === 'parent')) setSelectedId(a.id); }}
                                    >
                                      {a.child.name}
                                    </span>
                              </div>
                            ))}
                            {assigns.length > 2 && (
                              <button
                                onClick={() => { setDayModalAssignments(assigns); setDayModalDate(new Intl.DateTimeFormat(locale).format(day)); setDayModalOpen(true); }}
                                className="text-xs text-gray-500 px-2 py-0.5 rounded-full bg-gray-100 hover:bg-gray-200"
                                title={`Voir ${assigns.length - 2} autres enfants`}
                              >
                                +{assigns.length - 2}
                              </button>
                            )}
                          </>
                        )}
                      </td>
                    );
                  })}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {saveError && (
        <div className="text-red-600 font-semibold mb-2">{saveError}</div>
      )}
      {modalOpen && (
        <div className="fixed inset-0 z-40 bg-white/40 backdrop-blur-sm transition-all"></div>
      )}
      <AssignmentModal
        open={modalOpen}
        onClose={() => { setModalOpen(false); setSelectedId(null); setModalInitial(null); setSaveError(null); }}
        onSave={handleSave}
        initial={modalInitial || undefined}
      />
      {selectedId && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm">
          <div className="bg-white rounded-2xl shadow-xl p-6 w-full max-w-sm relative flex flex-col items-center">
            <button onClick={() => setSelectedId(null)} className="absolute top-2 right-2 text-gray-400 hover:text-gray-700 text-2xl" aria-label="Fermer">Ã—</button>
            <div className="flex flex-col items-center gap-4">
                <div className="bg-red-100 rounded-full p-4 mb-2">
                  <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" /></svg>
                </div>
                <h3 className="text-xl font-bold text-gray-900 mb-2 text-center">{t('modal.delete.title')}</h3>
                {/* Render child name and date on separate lines for better presentation */}
                {selectedAssignment && (
                  <div className="text-center">
                    <p className="text-gray-700 font-semibold">{`Voulez-vous supprimer ${selectedAssignment.child.name}`}</p>
                    <p className="text-gray-700 font-semibold mt-1">{(() => {
                      const d = modalInitial?.date || selectedAssignment.date;
                      let formatted = d;
                      try {
                        formatted = new Intl.DateTimeFormat(locale, { year: 'numeric', month: 'long', day: 'numeric' }).format(new Date(d));
                      } catch {
                        /* ignore formatting errors */
                      }
                      return `le ${formatted} ?`;
                    })()}</p>
                  </div>
                )}
              <div className="flex gap-3 w-full">
                <button
                  onClick={() => setSelectedId(null)}
                  className="flex-1 px-4 py-2 rounded-xl bg-gray-100 text-gray-700 font-semibold hover:bg-gray-200 transition"
                >{t('modal.cancel')}</button>
                <button
                  onClick={async () => {
                    await fetchWithRefresh(`api/assignments/${selectedId}`, {
                      method: 'DELETE',
                      credentials: 'include',
                    });
                    setSelectedId(null);
                    setModalInitial(null);
                    const year = currentDate.getFullYear();
                    const monthIdx = currentDate.getMonth();
                    const first = new Date(year, monthIdx, 1);
                    const last = new Date(year, monthIdx + 1, 0);
                    fetchAssignments(first, last);
                  }}
                  className="flex-1 px-4 py-2 rounded-xl bg-gradient-to-r from-red-500 via-red-600 to-red-700 text-white font-bold shadow-lg hover:from-red-600 hover:to-red-800 focus:outline-none focus:ring-2 focus:ring-red-400 focus:ring-offset-2 transition-all duration-150"
                  aria-label="Supprimer lâ€™affectation"
                >
                  {t('modal.delete.confirm')}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
      {dayModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/30 backdrop-blur-sm">
          <div className="bg-white rounded-2xl shadow-xl p-6 w-full max-w-md relative">
            <button onClick={() => setDayModalOpen(false)} className="absolute top-2 right-2 text-gray-400 hover:text-gray-700 text-2xl">Ã—</button>
            <h2 className="text-xl font-bold mb-1 text-center">{t('page.children')}</h2>
            <div className="text-sm text-gray-500 mb-4 text-center">{dayModalDate}</div>
            {dayModalAssignments.length === 0 ? (
              <div className="text-gray-500 text-center">{t('children.none_on_date', { date: dayModalDate })}</div>
            ) : (
              <div className="space-y-4 max-h-64 overflow-y-auto">
                {Object.entries(dayModalAssignments.reduce((acc, a) => {
                  if (!acc[a.nanny.name]) acc[a.nanny.name] = [];
                  acc[a.nanny.name].push(a);
                  return acc;
                }, {} as Record<string, Assignment[]>)).map(([nannyName, assigns]) => (
                  <div key={nannyName}>
                    <div className="font-bold text-blue-700 mb-2 text-center">{nannyName}</div>
                    <ul className="space-y-2">
                      {assigns.map(a => (
                        <li
                          key={a.id}
                          className="bg-blue-50 rounded px-3 py-2 text-gray-800 font-semibold text-center border border-blue-100 cursor-pointer hover:bg-blue-100 flex items-center justify-between"
                          title={t('dashboard.click_to_delete_assignment')}
                          onClick={() => {
                            // reuse existing delete modal: set selectedId and close the day modal
                            setSelectedId(a.id);
                            // close the day modal to show the global delete modal
                            setDayModalOpen(false);
                            // set modal initial so the delete modal shows correct child/date context
                            setModalInitial({ date: a.date.split('T')[0], childId: a.child.id, nannyId: a.nanny.id });
                          }}
                        >
                          <span>{a.child.name}</span>
                          <span className="text-xs text-red-600 ml-2">{t('modal.delete.confirm')}</span>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      )}
      </div>
    </div>
  );
}

